mantic;\r\n    @include mixin.loader_style;\r\n    margin: 30px 0;\r\n    position: relative;\r\n    padding: 30px;\r\n    .gswpts_table_loader {\r\n        width: 100%;\r\n        min-height: 200px;\r\n    }\r\n    table {\r\n        th,\r\n        td {\r\n            max-width: 200px;\r\n            min-width: 90px;\r\n            overflow-wrap: break-word;\r\n            border: 1px solid #76767647;\r\n        }\r\n        @media screen and (max-width: 768px) {\r\n            th,\r\n            td {\r\n                max-width: unset;\r\n            }\r\n        }\r\n\r\n        .expanded_style {\r\n            max-width: unset;\r\n            overflow-wrap: unset;\r\n            white-space: nowrap;\r\n            overflow-x: unset;\r\n            @media screen and (max-width: 768px) {\r\n                & {\r\n                    white-space: normal;\r\n                }\r\n            }\r\n        }\r\n        .clip_style {\r\n            max-width: 200px;\r\n            overflow-wrap: unset;\r\n            white-space: nowrap;\r\n            overflow-x: overlay;\r\n            height: 30px;\r\n        }\r\n        .wrap_style {\r\n            max-width: 200px;\r\n            overflow-wrap: break-word;\r\n            white-space: normal;\r\n            overflow-x: unset;\r\n            @media screen and (max-width: 768px) {\r\n                & {\r\n                    max-width: unset;\r\n                }\r\n            }\r\n        }\r\n\r\n        ::-webkit-scrollbar {\r\n            width: 10px;\r\n        }\r\n\r\n        ::-webkit-scrollbar-track {\r\n            box-shadow: inset 0 0 10px 10px rgb(34 36 38 / 10%);\r\n            border: solid 5px transparent;\r\n        }\r\n\r\n        ::-webkit-scrollbar-thumb {\r\n            box-shadow: inset 0 0 10px 10px rgb(34 36 38 / 10%);\r\n            border: solid 5px transparent;\r\n        }\r\n\r\n        th:focus {\r\n            outline: none;\r\n        }\r\n    }\r\n    select {\r\n        width: 90px;\r\n        border-radius: 5px;\r\n        border: 1px solid #00000033 !important;\r\n        background: transparent !important;\r\n    }\r\n    .dt-buttons {\r\n        display: flex;\r\n        justify-content: flex-end;\r\n        margin-top: 5px;\r\n        gap: 10px;\r\n        button {\r\n            font-size: 13px !important;\r\n            margin: 0 6px;\r\n            margin-right: 5px;\r\n            &:hover img {\r\n                filter: brightness(0.1);\r\n            }\r\n        }\r\n        span {\r\n            display: flex;\r\n            justify-content: center;\r\n            align-items: center;\r\n            height: 21px;\r\n        }\r\n        .export_btns {\r\n            display: none;\r\n        }\r\n    }\r\n    .gswpts_tables_content {\r\n        table {\r\n            width: 100% !important;\r\n        }\r\n        .filtering_input {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin: 15px 0 5px 0;\r\n            #create_tables_length {\r\n                margin-right: auto;\r\n            }\r\n            #create_tables_filter {\r\n                margin-left: auto;\r\n            }\r\n            input {\r\n                background: transparent !important;\r\n            }\r\n        }\r\n        .bottom_options {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin-top: 10px;\r\n            .dataTables_info {\r\n                padding-top: 0 !important;\r\n                margin-right: auto;\r\n            }\r\n            #create_tables_paginate {\r\n                margin-left: auto;\r\n                padding: 0 5px;\r\n            }\r\n            .pagination.menu {\r\n                padding: 0 !important;\r\n                font-size: 14px !important;\r\n                background: transparent !important;\r\n                display: flex;\r\n            